/* @import url(https://fonts.googleapis.com/css?family=Roboto:500,700); */
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


*, *::before, *::after {
  box-sizing: border-box;
}

html {
  min-height: 100%;
  font-family: "Segoe","Segoe UI","Arial","sans-serif";
}



body {
  margin: 20px;
  color: #435757;
  background: linear-gradient(-20deg, #d0b782 20%, #a0cecf 80%);
  font: 300 1.2em/1.2 'Roboto', sans-serif;
  
}

.container{
display: flex;
flex-direction: column;
width: 40%;
border-top: 5px solid #435757;
padding:0;
box-shadow: 0 0 20px rgba(0, 0, 0, .1);
}

h1{
 padding:20px;
 background-color: rgba(255, 255, 255, .4);
 text-align: center;
}
h2{
  position: relative;

}

.done{
  order:1;
  margin-top:10px;
  margin-bottom: 15px;
}
h2::before{
  content:'';
  display: block;
  position: absolute;
  top: 10px;
  bottom: 10px;
  left: -20px;
  width: 5px;
  background-color: #435757; 
}
h2::after {
  display: block;
  float: right;
  font-weight: normal;
}



.items{
  padding: 0 20px 20px 20px;
  counter-reset: done-items undone-items;
  display:flex;
  flex-direction: column;

}

