label{  
  display: block;
  border-top: 1px dashed #fff;
  cursor: pointer;
  order: 2;
  position: relative;
  padding: 15px 0 15px 45px;
  margin-bottom: 0;
  /* animation: undone .5s; */
}


/* label:hover, input:focus + label {
  background-color: rgba(255, 255, 255, .2);
} */

label:hover{
  background-color: rgba(255, 255, 255, .2);
}

.uncheck {
  order: 2;
}

/* input:checked + label {
  order: 4;
  animation: done .5s;
} */
/* input:checked + label::before {
  content: '\f058'; 
} */


.undone{
  order:1;
  margin-top: 10px;
}
@keyframes done{
  0%{
    opacity: 0;
    background-color: rgba(255, 255, 255, .4);
    transform: translateY(20px);
  }
  50% {
    opacity: 1;
    background-color: rgba(255, 255, 255, .4);
  }

}

@keyframes undone {
  0% {
    opacity: 0;
    background-color: rgba(255, 255, 255, .4);
    transform: translateY(-20px);
  }
  50% {
    opacity: 1;
    background-color: rgba(255, 255, 255, .4);
  }
}

.add{
  position: relative;
  padding-bottom: 0!important;
}

.right-input{
  position: absolute;
  right: 17px;
  top: 11px;
}
.color-font{
  color: #435757;
  font-size: 1.5rem;
}



.badge{
  position: absolute;
  right: 113px;
}
.no-padding{
 padding-left: 0!important;
}
ul {
  list-style-type: none;
}

.location{
  position: absolute;
  top:50px;
}

checked{
  order: 4;
  /* animation: done .5s; */
}