/* @import url(https://fonts.googleapis.com/css?family=Roboto:500,700); */
.App {
    text-align: center;
  }
  
  .App-logo {
    height: 40vmin;
    pointer-events: none;
  }
  
  @media (prefers-reduced-motion: no-preference) {
    .App-logo {
      animation: App-logo-spin infinite 20s linear;
    }
  }
  
  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .App-link {
    color: #61dafb;
  }
  
  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
  
  
  *, *::before, *::after {
    box-sizing: border-box;
  }
  
  html {
    min-height: 100%;
    font-family: "Segoe","Segoe UI","Arial","sans-serif";
  }
  
  
  
  body {
    margin: 20px;
    color: #435757;
    background: linear-gradient(-20deg, #d0b782 20%, #a0cecf 80%);
    font: 300 1.2em/1.2 'Roboto', sans-serif;
    
  }
  
  .container{
  display: flex;
  flex-direction: column;
  width: 40%;
  border-top: 5px solid #435757;
  padding:0;
  box-shadow: 0 0 20px rgba(0, 0, 0, .1);
  }
  
  h1{
   padding:20px;
   background-color: rgba(255, 255, 255, .4);
   text-align: center;
  }
  h2{
    position: relative;
  
  }
  
  .done{
    order:3;
    margin-top:10px;
    margin-bottom: 15px;
  }
  h2::before{
    content:'';
    display: block;
    position: absolute;
    top: 10px;
    bottom: 10px;
    left: -20px;
    width: 5px;
    background-color: #435757; 
  }
  h2::after {
    display: block;
    float: right;
    font-weight: normal;
  }
  
  
  
  .items{
    padding: 0 20px 20px 20px;
    display:flex;
    flex-direction: column;
  
  }
  
  
  
  
  
  
  label{  
    display: block;
    border-top: 1px dashed #fff;
    cursor: pointer;
    /* order: 2;  */
    position: relative;
    padding: 15px 0 15px 45px;
    margin-bottom: 0;
    /* animation: undone .5s; */
  }
  
  
  label:hover{
    background-color: rgba(255, 255, 255, .2);
  }
  
  .edit{
    border:none;
    background: none;
    width:380px;
    order: 4;
    /* animation:done .5s; */
  }
  
  
  
  @keyframes done{
    0%{
      opacity: 0;
      background-color: rgba(255, 255, 255, .4);
      transform: translateY(20px);
    }
    50% {
      opacity: 1;
      background-color: rgba(255, 255, 255, .4);
    }
  
  }
  
  @keyframes undone {
    0% {
      opacity: 0;
      background-color: rgba(255, 255, 255, .4);
      transform: translateY(-20px);
    }
    50% {
      opacity: 1;
      background-color: rgba(255, 255, 255, .4);
    }
  }
  
  .add{
    position: relative;
    padding-bottom: 0!important;
  }
  
  .right-input{
    position: absolute;
    right: 17px;
    top: 11px;
  }
  .color-font{
    color: #435757;
    font-size: 1.5rem;
  }
  .uncheck {
    order: 2;
    /* animation: undone .5s; */
  }
  .location{
    position: absolute;
    top: 0;
    left: 0;
  }
  
  .badge{
    position: absolute;
    right: 113px;
  }
  .no-padding{
   padding-left: 0!important;
  }
  ul {
    list-style-type: none;
  }


  .completed{
    /* text-decoration: line-through; */
    order: 4;
    /* animation: done .5s; */
  }
  .decor{
    text-decoration:line-through;
  }